.textarea {
  background-color: rgb(35, 34, 35);
  cursor: no-drop;
  border: 2px solid rgb(35, 34, 35);
  border-radius: 4px;
  -moz-appearance: none;
  outline: 0px none transparent;
  content: "";
  width: 100%;
  height: 400px;
  color: gray;
  overflow-Y: scroll;

}

::-webkit-scrollbar {
  display: none;

}

textarea:active {
  cursor: no-drop;
}

.Supportbutton {
  font-size: 20px;
}

.fatchtable {
  line-height: 10%;
  font-size: 19px;
}
.my-custom-scrollbar {
  width: 100%;
  height: 400px;
  overflow-y: scroll
}
.table-borderless > tbody > tr > td,
.table-borderless > tbody > tr > th,
.table-borderless > tfoot > tr > td,
.table-borderless > tfoot > tr > th,
.table-borderless > thead > tr > td,
.table-borderless > thead > tr > th {
    border: none;
}

@media(max-width: 1200px) {
  .Supportbutton {
    font-size: 13px;
  }

  .my-custom-scrollbar {
    width: auto;
    height: 400px;
    overflow-x: scroll
  }

  .fatchtable {
    line-height: 40%;
    font-size: 10px;
  }
}

@media(min-width: 300px)and (max-width: 400px) {
  .fatchtable {
    line-height: 40%;
    font-size: 30px;
  }
  .statuscss{
    padding-left: 29px
  }
}